import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { InputLabel, FormControl, TextField } from "@material-ui/core";
import "./CustomizedInputs.scss";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    fontFamily: "Poppins",
  },
  input: {
    "&::placeholder": {
      textOverflow: "ellipsis !important",
      color: "#363636",
    },
  },
}));

function CustomizedInputs(props) {
  const classes = useStyles();

  let icon = null;
  if (props.inputIcon) {
    icon = (
      <img
        src={props.inputIcon}
        height={props.imageHeight}
        width={props.imageWidth}
        style={{ paddingLeft: "10px", marginRight: "-1px" }}
        role="button"
        onClick={props.imageOnClick}
      />
    );
  }



  return (

      <div style={{width:'100%'}}>
       {
         props.labelName ?  <InputLabel
         htmlFor="bootstrap-input"
         className={
           props.noMargin
             ? "text-black ml-2 mb-2"
             : "text-black ml-2 mb-2 "
         }
         style={{ fontFamily: "Poppins", color: "#162855" }}
       >
         {props.required ? `${props.labelName} *` : props.labelName}
       </InputLabel> : null
       }
        <FormControl
          className={classes.margin}
          style={props.deviceAddInput ? { width: "88%" } : { width: "100%" }}
          error={props.error}
        >
          <TextField
          className="new-input"
            id="bootstrap-input"
            name={props.name ? props.name : ""}
            placeholder={props.placeholder}
            onChange={props.onChange}
            type={props.type ? props.type : "text"}
            style={{ font: "normal normal normal 14px/21px Poppins" }}
            className={`${props.className} input-fields`}
            size={props.size ? props.size : "small"}
            error={props.error}
            required={props.required}
            inputProps={{
              maxLength: props.inputProps ? props.inputProps : 200,
        
              
            }}
            helperText={props.helperText}
            InputProps={{
              className: classes.textInput,
            }}
            InputProps={{
              startAdornment: icon,
            }}
            value={props.value}
            variant="outlined"
            height='34px'
            disabled={props.disabled}
            onBlur={props.onKeyDown}
            onKeyPress={(e) => {
              e.key === "Enter" && e.preventDefault();
            }}
          />
        </FormControl>
      </div>
 
  );
}

export default CustomizedInputs;
