import React from "react";
import { connect } from "react-redux";
import * as Actions1 from "../../store/Feature1/FeatureAction";
import * as Actions from "../../store/Candidate/CandidateAction";
import { bindActionCreators } from "redux";
import TableComponent from "../../components/Controls/Table/Table";
import "./showCandidates.css";
import CustomizedInputs from "../../components/Atoms/CustomizedInputs/CustomizedInputs";
import axios from "axios";
import serverAddress from "../../config";
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom";
import Button from "../../components/Controls/Button/Button";
import Checkbox from "@material-ui/core/Checkbox";
import ReactGA from "react-ga";

class showCandidates extends React.Component {
  state = {
    open1: false,
    setOpen1: false,
    job: "",
    page: 1,
    limit: 5,
    value: "",
    id: "",
    name: "",
    openRes: "",
    cdata: [],
  };

  handleopen = () => {
    this.setState({ open1: true });
  };

  handleClose = () => {
    this.setState({ open1: false });
  };

  handleDeleteCandidate = (val) => {
    const payload = {
      data: {
        isDeleted: true,
      },
      id: val.original.id,
    };
    // this.props.CandidateAction.deleteCandidateData(payload);
  };

  componentDidMount() {
    ReactGA.pageview(window.location.pathname);
    this.props.FeatureAction.actionRequest();
    const payload = {
      page: this.state.page,
      limit: this.state.limit,
    };
    this.props.CandidateAction.getCandidateData(payload);
    this.getOptions();
  }

  async getOptions() {
    const token = localStorage.getItem("token");
    const res = await axios.get(`${serverAddress}/api/Jobtitle/get`, {
      headers: { Authorization: `Bearer ${token}` },
    });
    const data = res.data.jobTitles;
    const options = data.map((d) => ({
      value: d.jobtitle,
      label: d.jobtitle,
    }));
    this.setState({ options: options });
  }

  handleSearch = (e) => {
    this.setState({ value: e.value, searchItem: e.target.value });
  };

  handleSelect = (e) => {
    this.setState({ job: e.value });
  };

  handlePagination = async (e) => {
    await this.setState({ page: e.selected + 1 });

    const payload = {
      page: this.state.page,
      limit: this.state.limit,
    };
    this.props.CandidateAction.getCandidateData(payload);
  };

  handlesearchCandidate = (e) => {
    const payload = {
      name: this.state.searchItem,
    };
    this.props.CandidateAction.searchCandidateData(payload);
  };

  render() {
    let totalPage = 1;
    if (
      this.props &&
      this.props.CandidateReducer &&
      this.props.CandidateReducer.getCandidateData &&
      this.props.CandidateReducer.getCandidateData.data &&
      this.props.CandidateReducer.getCandidateData.data.candidate
    ) {
      let data1 = [];
      totalPage =
        this.props.CandidateReducer.getCandidateData.data.totalCandidate /
        this.state.limit;
      this.props.CandidateReducer.getCandidateData.data.candidate.map(
        (item) => {
          data1.push({
            name: item.name,
            email: item.email,
            id: item._id,
            phone: item.phone,
          });
        }
      );
      this.state.cdata = data1;
    }

    let inboundDatacolumn = [
      {
        Header: "Email Sent",
        // minWidth: 60,
        className: "resumeCell",
        Cell: (props) => (
          <div>
            {
              <Checkbox
                defaultChecked
                color='primary'
                inputProps={{ "aria-label": "secondary checkbox" }}
              />
            }
          </div>
        ),
      },

      {
        Header: "Name",
        accessor: "name",
        minWidth: 150,
        style: { textAlign: "left" },
      },
      {
        Header: "Email",
        accessor: "email",
        minWidth: 200,
        style: { textAlign: "left" },
      },
    ];
    return (
      <div className='show-cand-card'>
        <div className='show-cand-status'>
          <div className='card show-cand-cards col-lg-3'>
            <div className='show-tick'></div>
            <div className='show-text text1'>
              <h3> 200</h3>
              <p>Active Candidates</p>
            </div>
          </div>
          <div className='card show-cand-cards col-lg-3'>
            <div className='show-rejected'></div>
            <div className='show-text text2'>
              <h3> 50</h3>
              <p>Inactive Candidates</p>
            </div>
          </div>
          <div className='card show-cand-cards col-lg-3'>
            <div className='show-total'></div>
            <div className='show-text text3'>
              <h3> 250</h3>
              <p>Total Candidates</p>
            </div>
          </div>
        </div>
        <div className='rs-search-div'>
          <CustomizedInputs
            className='rs-search-bar'
            imageOnClick={() => {}}
            value={this.state.value}
            placeholder='Search For CSV File'
            onChange={this.filterList}
          ></CustomizedInputs>
        </div>
        <TableComponent
          justifyContent='center'
          data={this.state.cdata}
          columns={inboundDatacolumn}
          handlePageClick={(e) => {
            this.handlePagination(e);
          }}
          totalPage={totalPage}
        />
        <div className='candidate-button'>
          <Link to='/Schedule'>
            <Button className='addJDbtn btnNewLine'>Add Candidate</Button>
          </Link>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    CandidateReducer: state.CandidateReducer,
    FeatureReducer: state.FeatureReducer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    CandidateAction: bindActionCreators(Actions, dispatch),
    FeatureAction: bindActionCreators(Actions1, dispatch),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(showCandidates));
